img {
  max-width: 100%;
  max-height: 250px;
}

/* Zoom In #1 */
.hover01 figure img {
	-webkit-transform: scale(1);
	transform: scale(1);
	transition: .3s ease-in-out;
}
.hover01 figure:hover img {
	-webkit-transform: scale(1.3);
	transform: scale(1.3);
}
.navbar-nav.navbar-center {
  position: absolute;
  left: 50%;
  -webkit-transform: translatex(-50%);
          transform: translatex(-50%);
}
.navbar-nav.navbar-right {
  position: absolute;
  left: 85%;
  /* transform: translatex(-50%); */
}

.navbar span {
  color:white;
}

